  position: relative;
}

.loader:after {
  content: "";
  height: 4px;
  width: 0%;
  background: #ff3d00;
  animation: lineGrow 5s linear infinite;
  display: block;
}

@keyframes lineGrow {
  to {
    width: 100%;
  }
}

.hidden {
  display: none;
